.HeroSection{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.HeroSection h1 span{
    color: #0057ff;
}

.buttons{
    display: flex;
    height: 100px;
    align-items: center;
    width: 300px;
}
.buttons button{
    background-color: #0057ff;
    border: none;
    border-radius: 2px;
    padding: 10px;
    height: 42px;
    cursor: pointer;
}
.buttons .hire{
    margin-left: 30px;
}
.heroImg{
    max-width: 100%;
    height: 400px;
    padding: 20px;
    overflow: hidden;
    /* background-color: gray; */
    border-radius: 100%;
}
.heroImg img{
    width: 100%;
    height: 100%;
    border-radius: 100%;
    margin-top: auto;
    border-radius: 100%;
}


@media(min-width: 992px) {
    .HeroSection h1{
        font-size: 5rem;
        margin-bottom: 30px;
    }
}


@media (max-width: 620px){
    .HeroSection{
        flex-direction: column;
    }
    .heroImg img{
        height: auto;
    }
}