.content{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.content .item{
    width: 250px;
    height: 250px;
    overflow: hidden;
    margin: auto;

}
.content .item img{
    width: 100%;
    height: 100%;
}